.wp-block-create-block-alumni-card-block {
	background-color: #21759b;
	padding: 2px;
}

.alumni__card {
	width: 600px;
	background-color: #ffebfb;
	.alumni__header__container {
		display: flex;
	}
	img {
		border-radius: 50%;
		height: 123px;
		width: 123px;
		margin: 25px;
	}
	.alumni__title__container {
		display: flex;
		justify-content: center;
		flex-direction: column;
	}
	.alumni__card__quote {
		text-align: center;
		margin: 15px;
	}
	.alumni__card__body {
		margin: 15px;
	}
}
